<template><section><h1>Message 消息提示</h1>
<h2>基础用法</h2>
<p>从顶部出现，3 秒后自动消失。</p>
<div class="demo-block">
<vi-button :plain="true" @click="openMsg1">打开消息提示</vi-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><vi-button :plain="true" @click="openMsg1">打开消息提示</vi-button>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg1"</span>&gt;</span>打开消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    openMsg1 () {
      <span class="hljs-keyword">this</span>.$message(<span class="hljs-string">'这是一条消息提示'</span>)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>文字居中</h2>
<div class="demo-block">
<vi-button :plain="true" @click="openMsg2">center 打开消息提示 </vi-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><vi-button :plain="true" @click="openMsg2">center 打开消息提示 </vi-button>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg2"</span>&gt;</span>center 打开消息提示 <span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    openMsg2 () {
      <span class="hljs-keyword">this</span>.$message({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'居中的文字'</span>,
        <span class="hljs-attr">center</span>: <span class="hljs-literal">true</span>
      })
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>不同状态</h2>
<div class="demo-block">
  <vi-button :plain="true" @click="openMsg3">成功</vi-button>
  <vi-button :plain="true" @click="openMsg4">警告</vi-button>
  <vi-button :plain="true" @click="openMsg5">消息</vi-button>
  <vi-button :plain="true" @click="openMsg6">错误</vi-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><vi-button :plain="true" @click="openMsg3">成功</vi-button>
  <vi-button :plain="true" @click="openMsg4">警告</vi-button>
  <vi-button :plain="true" @click="openMsg5">消息</vi-button>
  <vi-button :plain="true" @click="openMsg6">错误</vi-button>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg3"</span>&gt;</span>成功<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg4"</span>&gt;</span>警告<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg5"</span>&gt;</span>消息<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg6"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    openMsg3 () {
      <span class="hljs-keyword">this</span>.$message({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'恭喜你，这是一条成功消息'</span>,
        <span class="hljs-attr">type</span>: <span class="hljs-string">'success'</span>
      })
    ,
    openMsg4 () {
      <span class="hljs-keyword">this</span>.$message({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'警告哦，这是一条警告消息'</span>,
        <span class="hljs-attr">type</span>: <span class="hljs-string">'warning'</span>
      })
    },
    openMsg5 () {
      <span class="hljs-keyword">this</span>.$message({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一条消息提示'</span>,
        <span class="hljs-attr">type</span>: <span class="hljs-string">'info'</span>
      })
    },
    openMsg6 () {
      <span class="hljs-keyword">this</span>.$message.error(<span class="hljs-string">'错了哦，这是一条错误消息'</span>)
    },
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>可关闭</h2>
<div class="demo-block">
<vi-button :plain="true" @click="openMsg7">消息</vi-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><vi-button :plain="true" @click="openMsg7">消息</vi-button>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vi-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openMsg7"</span>&gt;</span>消息<span class="hljs-tag">&lt;/<span class="hljs-name">vi-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    openMsg7 () {
      <span class="hljs-keyword">this</span>.$message({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一条消息提示'</span>,
        <span class="hljs-attr">showClose</span>: <span class="hljs-literal">true</span>
      })
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>Options</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>消息文字</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/error</td>
<td>---</td>
</tr>
<tr>
<td>iconClass</td>
<td>自定义图标的类名，会覆盖 type</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>customClass</td>
<td>自定义类名</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>duration</td>
<td>显示时间, 毫秒。设为 0 则不会自动关闭</td>
<td>number</td>
<td>---</td>
<td>3000</td>
</tr>
<tr>
<td>showClose</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>center</td>
<td>文字是否居中</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭时的回调函数, 参数为被关闭的 message 实例</td>
<td>function</td>
<td>---</td>
<td>---</td>
</tr>
</tbody>
</table>
<h2>methods</h2>
<p>调用 Message 或 this.$message 会返回当前 Message 的实例。如果需要手动关闭实例，可以调用它的 close 方法。</p>
<p>方法名 | 说明
close | 关闭当前的 Message</p>
</section></template>

<script>
export default {
  methods: {
    openMsg1 () {
      this.$message('这是一条消息提示')
    }
  }
}
</script>


